.game-stage-page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0 30px;
  box-sizing: border-box;
}
.game-stage-page .game-stage-header {
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: solid 2px #666;
}
.game-stage-page .game-stage-container {
  width: 600px;
  max-width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}
.game-stage-page .game-users-container {
  flex-grow: 1;
  overflow-y: scroll;
  overflow-x: hidden;
}
.game-stage-page .game-users {
  display: flex;
  margin: 0 -15px;
  flex-wrap: wrap;
}
.game-stage-page .buttons {
  display: flex;
  margin: 0 -15px 30px -15px;
}
