.create-user-page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 30px;
  box-sizing: border-box;
}
.create-user-page .create-user-container {
  width: 600px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
}
.create-user-page .launch-buttons {
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
}
