.game-list-page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  padding: 0 30px;
  box-sizing: border-box;
}
.game-list-page .game-list-container {
  width: 600px;
  margin-top: 50px;
}
.game-list-page .game-list {
  display: flex;
  flex-direction: column;
}
.game-list-page .game-enter {
  opacity: 0.01;
  transform: translateX(20px) scale(0.95);
}
.game-list-page .game-enter.game-enter-active {
  opacity: 1;
  transform: translateX(0) scale(1);
  transition: opacity 400ms, transform 400ms;
}
.game-list-page .game-leave {
  opacity: 1;
}
.game-list-page .game-leave.game-leave-active {
  opacity: 0.01;
  transition: opacity 133.33333333ms;
}
.game-list-page .join-game-button {
  background: #ccc;
  color: #555;
  font-size: 22px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  width: 100%;
  font-weight: 300px;
  transition: background 0.2s, color 0.2s;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 20px;
  box-sizing: border-box;
}
.game-list-page .join-game-button.new-game-button {
  justify-content: center;
  padding: 15px 20px;
  background: #eee;
}
.game-list-page .join-game-button h2 {
  font-size: 22px;
  font-weight: 400;
}
.game-list-page .join-game-button h3 {
  font-size: 12px;
  font-weight: 400;
}
.game-list-page .join-game-button:disabled {
  color: #999;
  background: #ccc;
  cursor: default;
}
.game-list-page .join-game-button:not(:disabled):not(:first-child):hover {
  background: #ddd;
}
.game-list-page .join-game-button:not(:disabled):first-child:hover {
  background: #fbfbfb;
}
