.brush-palette {
  position: absolute;
  left: 0;
  top: 0;
  width: 100px;
  height: 100%;
  background: transparent;
  transition: background 0.2s;
}
.brush-palette:hover {
  background: rgba(255, 255, 255, 0.05);
}
.brush-palette:hover .brush {
  border-color: #7c7c7c;
}
.brush-palette .brushes {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 12px;
}
.brush-palette .brushes:after {
  content: '';
  width: 30%;
  height: 18px;
}
.brush-palette .brush {
  width: 38px;
  height: 38px;
  border-radius: 22px;
  margin: 1px;
  border: solid 3px #777;
}
.brush-palette .brush.brush-green {
  background: #499d72;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-green.active {
  border-color: #97cfb2;
}
.brush-palette .brush.brush-green:hover:not(.active) {
  background: #51ae7f;
  transform: scale(1.1);
}
.brush-palette .brush.brush-blue {
  background: #6090bc;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-blue.active {
  border-color: #bacfe2;
}
.brush-palette .brush.brush-blue:hover:not(.active) {
  background: #729dc4;
  transform: scale(1.1);
}
.brush-palette .brush.brush-yellow {
  background: #ccc768;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-yellow.active {
  border-color: #eceac7;
}
.brush-palette .brush.brush-yellow:hover:not(.active) {
  background: #d2ce7b;
  transform: scale(1.1);
}
.brush-palette .brush.brush-black {
  background: #383838;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-black.active {
  border-color: #787878;
}
.brush-palette .brush.brush-black:hover:not(.active) {
  background: #454545;
  transform: scale(1.1);
}
.brush-palette .brush.brush-black.active {
  border-color: #555;
}
.brush-palette .brush.brush-white {
  background: #e4e4e4;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-white.active {
  border-color: #ffffff;
}
.brush-palette .brush.brush-white:hover:not(.active) {
  background: #f1f1f1;
  transform: scale(1.1);
}
.brush-palette .brush.brush-red {
  background: #c94045;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-red.active {
  border-color: #e5a3a6;
}
.brush-palette .brush.brush-red:hover:not(.active) {
  background: #cf5458;
  transform: scale(1.1);
}
.brush-palette .brush.brush-orange {
  background: #dd783b;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-orange.active {
  border-color: #f0c3a8;
}
.brush-palette .brush.brush-orange:hover:not(.active) {
  background: #e18751;
  transform: scale(1.1);
}
.brush-palette .brush.brush-purple {
  background: #a76da7;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-purple.active {
  border-color: #d7bdd7;
}
.brush-palette .brush.brush-purple:hover:not(.active) {
  background: #b17db1;
  transform: scale(1.1);
}
.brush-palette .brush.brush-eraser {
  background: #555;
  transition: transform 0.2s;
}
.brush-palette .brush.brush-eraser.active {
  border-color: #959595;
}
.brush-palette .brush.brush-eraser:hover:not(.active) {
  background: #626262;
  transform: scale(1.1);
}
.brush-palette .brush-sizes {
  padding-top: 6px;
}
.brush-palette .brush-size-12 {
  width: 12px;
  height: 12px;
  border-radius: 9px;
  margin: 14px;
}
.brush-palette .brush-size-20 {
  width: 20px;
  height: 20px;
  border-radius: 13px;
  margin: 10px;
}
.brush-palette .brush-size-28 {
  width: 28px;
  height: 28px;
  border-radius: 17px;
  margin: 6px;
}
.brush-palette .brush-utilities {
  padding-top: 12px;
  display: flex;
  justify-content: center;
}
.brush-palette .brush-utilities i {
  color: #494949;
  padding-bottom: 6px;
  margin: 0 5px;
}
.brush-palette .brush-utilities i:hover:not(.active) {
  color: #333;
}
.brush-palette .brush-utilities i.active {
  color: #222;
  border-bottom: solid 3px #222;
}
.brush-palette .undo {
  font-size: 21px;
}
.brush-palette .brush-eraser {
  font-size: 24px;
}
.brush-palette .trash {
  font-size: 23px;
}
