.game-messages {
  position: absolute;
  bottom: 100px;
  left: 0;
  right: 170px;
  display: flex;
  align-items: center;
  flex-direction: column;
  transition: bottom 0.5s, left 0.5s;
}
.drawer-is-me .game-messages {
  bottom: 10px;
  left: 100px;
}
.game-messages > * {
  width: 400px;
  max-width: 100%;
}
.game-messages .game-text-message-container {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-start;
}
.game-messages .message-enter {
  height: 0;
}
.game-messages .message-enter.message-enter-active {
  height: 28px;
  transition: height 300ms;
}
.game-messages .message-leave {
  opacity: 1;
}
.game-messages .message-leave.message-leave-active {
  opacity: 0;
  transition: opacity 3000ms;
}
.game-messages .game-text-message {
  background: #333;
  border-radius: 3px;
  padding: 4px 8px;
  display: inline-block;
  margin-bottom: 8px;
  display: inline-flex;
  overflow: hidden;
}
.game-messages .game-text-message-user-icon {
  display: flex;
  justify-content: center;
  padding-top: 2px;
}
.game-messages .game-text-message-username-and-text {
  margin-left: 8px;
}
.game-messages .game-text-message-username {
  font-size: 9px;
  font-weight: 100;
  color: #bbb;
}
.game-messages .game-text-message-text {
  font-size: 12px;
  font-weight: 100;
  color: #eee;
}
.game-messages .game-text-message-text i {
  color: #499d72;
}
