.button {
  height: 80px;
  background: #ddd;
  color: #555;
  font-size: 22px;
  border: solid 2px transparent;
  border-radius: 3px;
  cursor: pointer;
  margin: 0 15px;
  flex-grow: 1;
  min-width: 100px;
  font-weight: 300px;
  transition: background 0.2s, color 0.2s;
  max-width: 50%;
}
.button:disabled {
  color: #999;
  background: #ccc;
  cursor: default;
}
.button:not(:disabled):hover {
  background: #eee;
}
.button.button-success {
  background: #499d72;
  color: #fff;
}
.button.button-success:not(:disabled):hover {
  background: #51ae7f;
}
.button.button-quiet {
  background: transparent;
  border-color: #fff;
  color: #fff;
}
.button.button-quiet:hover {
  background: rgba(255, 255, 255, 0.1);
}
