.pre-round-modal {
  position: absolute;
  left: 0;
  right: 170px;
  top: 0;
  display: flex;
  justify-content: center;
}
.drawer-is-me .pre-round-modal {
  left: 100px;
}
.pre-round-modal .pre-round-modal-container {
  background: #e4e4e4;
  padding: 10px 40px;
  min-width: 200px;
  border-radius: 3px;
  margin-top: 20px;
  text-align: center;
}
.pre-round-modal .round-label {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 10px;
}
.pre-round-modal .drawer-icon {
  font-size: 12px;
}
.pre-round-modal .drawer-label {
  font-size: 14px;
  font-weight: 400;
}
