@keyframes slide-in {
  100% {
    transform: translate(0);
    opacity: 1;
  }
}
.game-panel {
  position: absolute;
  right: 0;
  width: 170px;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translate(20%);
  opacity: 0;
  animation: slide-in 300ms forwards;
  background: transparent;
  transition: background 0.2s;
}
.game-panel:hover {
  background: rgba(255, 255, 255, 0.05);
}
.game-panel .game-panel-header {
  padding: 0 10px;
  margin: 15px 0;
  width: 100%;
  box-sizing: border-box;
}
.game-panel .game-name {
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.game-panel .round-label {
  font-size: 18px;
  color: #444;
  line-height: 1;
}
.game-panel .round-label .number {
  font-weight: 600;
}
.game-panel .round-timer {
  position: relative;
}
.game-panel .round-timer-children {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
