@keyframes pop-icon {
  100% {
    transform: scale(1.2);
  }
}
.user-icon {
  width: 80px;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 15px;
  align-items: center;
  cursor: default;
}
.user-icon.user-icon-tiny {
  width: 18px;
  height: 24px;
  margin: 0;
}
.user-icon.user-icon-tiny .user-avatar {
  width: 18px;
  height: 18px;
  font-size: 14px;
  border-width: 0;
}
.user-icon .user-avatar {
  align-items: center;
  justify-content: center;
  width: 54px;
  height: 54px;
  display: flex;
  background: #ccc;
  border-radius: 50%;
  font-size: 30px;
  border: solid 3px transparent;
}
.user-icon.user-icon-status-normal .user-avatar {
  border-color: #777;
}
.user-icon.user-icon-status-correct .user-avatar {
  border-color: #499d72;
  animation: pop-icon 0.3s alternate 2;
}
.user-icon .user-name-wrap {
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  display: flex;
  width: 80px;
}
.user-icon .user-name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.user-icon .total-user-points {
  display: none;
}
.user-icon:hover .total-user-points {
  display: inherit;
}
.user-icon:hover .user-points {
  display: none;
}
.user-icon .user-points {
  color: #499d72;
}
