@keyframes bounce {
  0% {
    opacity: 0.01;
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.round-word {
  position: absolute;
  left: 100px;
  right: 170px;
  bottom: 50px;
  font-size: 40px;
  font-weight: 800;
  text-align: center;
  color: #444;
}
.pre-round-modal-enter {
  transform: translateY(-120%);
}
.pre-round-modal-enter.pre-round-modal-enter-active {
  opacity: 1;
  animation: bounce 400ms 200ms;
}
.pre-round-modal-leave {
  opacity: 1;
}
.pre-round-modal-leave.pre-round-modal-leave-active {
  opacity: 0;
  transition: opacity 1000ms;
}
.brush-palette-enter {
  transform: translateX(-20%);
  opacity: 0.1;
}
.brush-palette-enter.brush-palette-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: opacity 300ms, transform 300ms;
}
.brush-palette-leave {
  opacity: 1;
  transform: translateX(0);
}
.brush-palette-leave.brush-palette-leave-active {
  opacity: 0;
  transform: translateX(-20%);
  transition: opacity 300ms, transform 300ms;
}
.game-text-field-enter {
  opacity: 0.01;
}
.game-text-field-enter.game-text-field-enter-active {
  opacity: 1;
  transition: opacity 300ms 700ms;
}
.game-text-field-leave {
  opacity: 1;
}
.game-text-field-leave.game-text-field-leave-active {
  opacity: 0;
  transition: opacity 300ms;
}
