.text-field {
  border: none;
  width: 100%;
  height: 70px;
  border-radius: 3px;
  box-sizing: border-box;
  padding: 0 20px;
  font-size: 26px;
  color: #fff;
  background: #444;
  margin-bottom: 30px;
  outline: none;
  font-weight: 100;
  transition: background 0.2s, color 0.2s;
}
.text-field:hover {
  background: #393939;
}
.text-field:focus {
  background: #333;
}
.text-field::-webkit-input-placeholder {
  color: #ddd;
}
.text-field::-moz-placeholder {
  color: #ddd;
}
