@import 'https://fonts.googleapis.com/css?family=Roboto+Slab:100';
@import 'https://fonts.googleapis.com/css?family=Roboto+Slab:300';
@import 'https://fonts.googleapis.com/css?family=Roboto+Slab:400';
@import 'https://fonts.googleapis.com/css?family=Roboto+Slab:700';
@keyframes loading-spinner {
  to {
    opacity: 1;
  }
}
* {
  font-family: 'Roboto Slab', sans-serif;
  color: #333;
}
.app {
  display: flex;
  background-image: linear-gradient(0deg, transparent 99%, rgba(0, 0, 0, 0.1) 100%), linear-gradient(90deg, transparent 99%, rgba(0, 0, 0, 0.1) 100%);
  background-size: 100px 100px;
}
.page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #777;
  overflow: hidden;
}
.page-enter {
  opacity: 0.01;
  transform: translateX(20px) scale(0.95);
}
.page-enter.page-enter-active {
  opacity: 1;
  transform: translateX(0) scale(1);
  transition: opacity 400ms, transform 400ms;
}
.page-leave {
  opacity: 1;
}
.page-leave.page-leave-active {
  opacity: 0.01;
  transition: opacity 133.33333333ms;
}
.app-loading {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #666;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-loading .spinner {
  opacity: 0;
  animation: loading-spinner 1s 1s forwards;
}
.app-loading .spinner i {
  font-size: 50px;
  color: #999;
}
